import React, { useState } from "react";

const Contact = () => {
    const [data,setData] = useState({
        fullname : "",
        phone : "",
        email : "",
        message : "",

    });

    const InputEvent = (event) => {
        const {name,value} = event.target;

        setData((preval) =>{
            return{
                ...preval,
                [name] : value,
            };
        });
    }

    const fromSubmit = (e) =>{
        e.preventDefault();
        alert(`My name is ${data.fullname} My phone number is ${data.phone} and email is ${data.email} Here is what I want to say ${data.message}`)
    }
    return(
        <>
            <section id="contact">
                <div className="container my-5">
                <h1 className="text-center mb-3">Contact Us</h1>
                    <div className="row">
                        <div className="col-md-6 col-10 mx-auto">
                            <form onSubmit={fromSubmit}>
                                <div className="mb-3">
                                    <label for="exampleFormControlInput1" className="form-label">Name</label>
                                    <input 
                                    type="text"
                                    name="fullname"
                                    value={data.fullname}
                                    onChange={InputEvent}
                                    className="form-control" 
                                    id="exampleFormControlInput1" 
                                    placeholder="Enter your Name"
                                    />
                                </div>
                                <div className="mb-3">
                                    <label for="exampleFormControlInput1" className="form-label">Phone</label>
                                    <input 
                                    type="number"
                                    name="phone"
                                    value={data.phone}
                                    onChange={InputEvent} 
                                    className="form-control" 
                                    id="exampleFormControlInput1" 
                                    placeholder="Enter your Number"/>
                                </div>
                                <div className="mb-3">
                                    <label for="exampleFormControlInput1" className="form-label">Email address</label>
                                    <input 
                                    type="email"
                                    name="email"
                                    value={data.email}
                                    onChange={InputEvent}
                                    className="form-control" 
                                    id="exampleFormControlInput1" 
                                    placeholder="Enter your Email"/>
                                </div>
                                <div className="mb-3">
                                    <label for="exampleFormControlTextarea1" className="form-label">Message</label>
                                    <textarea 
                                    className="form-control"
                                    name="message"
                                    value={data.message}
                                    onChange={InputEvent} 
                                    id="exampleFormControlTextarea1" 
                                    placeholder="Enter your Message" 
                                    rows="3"></textarea>
                                </div>
                                <div className="col-12">
                                    <button className="btn btn-outline-primary" type="submit">Send Message</button>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </section>
        </>
    );
}

export default Contact;